<template>
    <v-dialog
        v-model="dialog"
        width="500"
    >
        <v-card>
            <v-card-title>
                新建文件
            </v-card-title>

            <v-card-text>
                <v-form>
                    <v-text-field
                        label="文件名"
                        v-model.trim="name"
                    ></v-text-field>
                </v-form>
                <span class="red--text">
                    注意：新建文件会覆盖已有文件
                </span>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    color="primary"
                    text
                    @click="saveClick"
                >
                    保存
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      dialog: false,
      name: ""
    };
  },
  computed: mapGetters(["getFilePath"]),
  methods: {
    show() {
      this.name = "";
      this.dialog = true;
    },
    saveClick() {
      const { name } = this;
      if (!name) {
        return this.$toast("请输入名称");
      }
      window.ha
        .post({
          type: "new-file",
          path: this.getFilePath(name)
        })
        .then(({ code, msg }) => {
          this.$toast(msg);
          if (code === 0) {
            this.dialog = false;
          }
        });
    }
  }
};
</script>
